@use "../../assets/sass/variables" as *;

.nav {
  display: flex;
  justify-content: space-between;
  padding: 2rem 8rem 1rem 8rem;
  width: 100%;

  &__title {
    color: $color-blue;
    font-size: 2.5rem;
    letter-spacing: 0.3rem;
  }

  &__list {
    display: flex;
    list-style: none;

    .nav__link {
      margin-left: 5rem;
      font-size: 1.2rem;
      font-weight: 700;
      color: $color-dark-grey;
      text-decoration: none;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;

      .nav__text {
        padding-left: 0.3rem;
      }
    }
  }
}
